import './App.css';
import './Navbar.css'

const nav={
    backgroundColor:'black',
    color:'blue'
}
function Navbar(){
return(

<nav className="fondoNav navbar navbar-expand-lg navbar-light bg-primary fs-3" >
    <div className="container-fluid">
        <a className="navbar-brand fs-2" href="https://www.google.es/">Navbar</a>
        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
        <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
            <a className="nav-link active" aria-current="page" href="https://www.google.es/">Home</a>
            </li>
            <li className="nav-item active">
            <a className="nav-link active" href="https://www.google.es/">Link</a>
            </li>
            <li className="nav-item dropdown">
            <a className="nav-link dropdown-toggle active" href="https://www.google.es/" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                Dropdown
            </a>
            <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                <li><a className="dropdown-item" href="https://www.google.es/">Action</a></li>
                <li><a className="dropdown-item" href="https://www.google.es/">Another action</a></li>
                <li><a className="dropdown-item" href="https://www.google.es/">Something else here</a></li>
            </ul>
            </li>
        </ul>
        <form className="d-flex">
            <input className="form-control me-2" type="search" placeholder="Search" aria-label="Search"></input>
            <button className="btn btn-outline-success" type="submit">Search</button>
        </form>
        </div>
    </div>
</nav>
    );
}
export default Navbar;